exports.push([module.id, "\n.main-content-wrap[data-v-5be44211] {\n  padding: 10px;\n}\n.search-bar[data-v-5be44211] {\n  padding-top: 10px;\n  padding-bottom: 10px;\n}\n.demo-table-expand[data-v-5be44211] {\n  font-size: 12px;\n}\n.demo-table-expand label[data-v-5be44211] {\n  color: #99a9bf;\n}\n.demo-table-expand .el-form-item[data-v-5be44211] {\n  margin-right: 0;\n  margin-bottom: 0;\n  width: 47%;\n}\n.e-form .el-form-item[data-v-5be44211] {\n  margin-right: 0;\n  margin-bottom: 0;\n  width: 45%;\n}\n.filelist-toolbar[data-v-5be44211] {\n  margin-left: 1px;\n}\n.fileView-content[data-v-5be44211] {\n  width: 100%;\n  border: 1px solid #ccc;\n  padding: 5px;\n  line-height: 20px;\n}\n.input-320[data-v-5be44211] {\n  width: 320px;\n}\n.form-title[data-v-5be44211] {\n  font-size: 24px;\n  font-weight: bold;\n  margin-left: 6%;\n  padding: 10px 0 5px 0;\n}\n.form-panel[data-v-5be44211] {\n  width: 90%;\n  margin-left: 5%;\n  padding: 20px 0 0 0;\n  border: 2px solid #ccc;\n  border-radius: 4px;\n  background-color: #fff;\n}\n.goods-panel[data-v-5be44211] {\n  width: 90%;\n  margin-left: 5%;\n  border: 2px solid #ccc;\n  border-radius: 4px;\n  background-color: #fff;\n}\n", "", {"version":3,"sources":["G:/git/declarationForm/mainUI/source/views/setting/license.vue?b83f8690"],"names":[],"mappings":";AA8vBA;EACA,cAAA;CACA;AAEA;EACA,kBAAA;EACA,qBAAA;CACA;AAEA;EACA,gBAAA;CACA;AAEA;EACA,eAAA;CACA;AAEA;EACA,gBAAA;EACA,iBAAA;EACA,WAAA;CACA;AAEA;EACA,gBAAA;EACA,iBAAA;EACA,WAAA;CACA;AAEA;EACA,iBAAA;CACA;AAEA;EACA,YAAA;EACA,uBAAA;EACA,aAAA;EACA,kBAAA;CACA;AAEA;EACA,aAAA;CACA;AAEA;EACA,gBAAA;EACA,kBAAA;EACA,gBAAA;EACA,sBAAA;CACA;AAEA;EACA,WAAA;EACA,gBAAA;EACA,oBAAA;EACA,uBAAA;EACA,mBAAA;EACA,uBAAA;CACA;AAEA;EACA,WAAA;EACA,gBAAA;EACA,uBAAA;EACA,mBAAA;EACA,uBAAA;CACA","file":"license.vue","sourcesContent":["<template>\n  <div>\n    <el-tabs v-model=\"activeName\" class=\"projPage\">\n      <!-- 进口 -->\n      <el-tab-pane label='进口' name='inlicense'>\n        <el-toolbar>\n          <el-button class=\"z-toolbar-btn\" :plain=\"true\" @click=\"inAddClick\">\n            <i class=\"fa fa-plus\"></i>添加</el-button>\n          <el-button class=\"z-toolbar-btn\" :plain=\"true\" :disabled=\"inSelectedRows.length !== 1\" @click=\"inEditClick\">\n            <i class=\"fa fa-edit\"></i>编辑</el-button>\n          <el-button class=\"z-toolbar-btn\" :plain=\"true\" :disabled=\"inSelectedRows.length < 1\" @click=\"inDeleteClick\">\n            <i class=\"fa fa-trash-o\"></i>删除</el-button>\n        </el-toolbar>\n        <div class=\"main-content-wrap\">\n          <div class=\"search-bar fr\">\n            许可证号:\n            <el-input v-model=\"insearch.licenseKey\" size=\"small\" placeholder=\"请输入许可证号\" style=\"width: 200px;\"></el-input>\n            进口商：\n            <el-input v-model=\"insearch.companyName\" size=\"small\" placeholder=\"请输入进口商\" style=\"width: 200px;\"></el-input>\n            <el-button size=\"small\" type=\"primary\" @click=\"loadInlicenseList\"> 搜 索 </el-button>\n          </div>\n          <el-table ref=\"myTabelRef\" :data=\"inLicenseData\" tooltip-effect=\"dark\" style=\"width: 100%\" :height=\"clientHeight\" @selection-change=\"inOnSelectionChange\" @row-click=\"onInRowClick\" @row-dblclick=\"dbInEditClick\">\n            <el-table-column type=\"selection\" width=\"55\" align=\"center\"></el-table-column>\n            <el-table-column prop=\"licenseKey\" show-overflow-tooltip min-width=\"20%\" label=\"许可证号\">\n              <template slot-scope=\"scope\">\n                <a @click=\"dbInEditClick(scope.row)\" class=\"a-btn\">{{scope.row.licenseKey}}</a>\n              </template>\n            </el-table-column>\n            <el-table-column prop=\"count\" show-overflow-tooltip min-width=\"20%\" label=\"许可数量\"></el-table-column>\n            <el-table-column prop=\"companyName\" show-overflow-tooltip min-width=\"20%\" label=\"进口商\"></el-table-column>\n            <el-table-column prop=\"skuName\" show-overflow-tooltip min-width=\"20%\" label=\"商品名称\"></el-table-column>\n            <el-table-column prop=\"expirationDateOfLicense\" show-overflow-tooltip min-width=\"20%\" label=\"许可证截止日期\"></el-table-column>\n          </el-table>\n          <div class=\"fr\" style=\"margin-top:5px;\">\n            <el-pagination @size-change=\"myHandleSizeChange\" @current-change=\"myHandleCurrentChange\" :current-page=\"myCurrentPage\" :page-sizes=\"myPageSizes\" :page-size=\"myPageSize\" :total=\"myTotal\" layout=\"total, sizes, prev, pager, next\">\n            </el-pagination>\n          </div>\n        </div>\n      </el-tab-pane>\n      <!-- 出口 -->\n      <el-tab-pane label='出口' name='outlicense'>\n        <el-toolbar>\n          <el-button class=\"z-toolbar-btn\" :plain=\"true\" @click=\"outAddClick\">\n            <i class=\"fa fa-plus\"></i>添加</el-button>\n          <el-button class=\"z-toolbar-btn\" :plain=\"true\" :disabled=\"outSelectedRows.length !== 1\" @click=\"outEditClick\">\n            <i class=\"fa fa-edit\"></i>编辑</el-button>\n          <el-button class=\"z-toolbar-btn\" :plain=\"true\" :disabled=\"outSelectedRows.length < 1\" @click=\"outDeleteClick\">\n            <i class=\"fa fa-trash-o\"></i>删除</el-button>\n        </el-toolbar>\n        <div class=\"main-content-wrap\">\n          <div class=\"search-bar fr\">\n            许可证号:\n            <el-input v-model=\"outsearch.licenseKey\" size=\"small\" placeholder=\"请输入许可证号\" style=\"width: 200px;\"></el-input>\n            出口商：\n            <el-input v-model=\"outsearch.companyName\" size=\"small\" placeholder=\"请输入出口商\" style=\"width: 200px;\"></el-input>\n            <el-button size=\"small\" type=\"primary\" @click=\"loadOutlicenseList\"> 搜 索 </el-button>\n          </div>\n          <el-table ref=\"apTabelRef\" :data=\"outLicenseData\" tooltip-effect=\"dark\" style=\"width: 100%\" :height=\"clientHeight\" @selection-change=\"outOnSelectionChange\" @row-click=\"onOutRowClick\" @row-dblclick=\"dbOutEditClick\">\n            <el-table-column type=\"selection\" width=\"55\" align=\"center\"></el-table-column>\n            <el-table-column prop=\"licenseKey\" show-overflow-tooltip min-width=\"20%\" label=\"许可证号\">\n              <template slot-scope=\"scope\">\n                <a @click=\"dbOutEditClick(scope.row)\" class=\"a-btn\">{{scope.row.licenseKey}}</a>\n              </template>\n            </el-table-column>\n            <el-table-column prop=\"count\" show-overflow-tooltip min-width=\"20%\" label=\"许可数量\"></el-table-column>\n            <el-table-column prop=\"companyName\" show-overflow-tooltip min-width=\"20%\" label=\"出口商\"></el-table-column>\n            <el-table-column prop=\"skuName\" show-overflow-tooltip min-width=\"20%\" label=\"商品名称\"></el-table-column>\n            <el-table-column prop=\"expirationDateOfLicense\" show-overflow-tooltip min-width=\"20%\" label=\"许可证截止日期\"></el-table-column>\n          </el-table>\n          <div class=\"fr\" style=\"margin-top:5px;\">\n            <el-pagination @size-change=\"apHandleSizeChange\" @current-change=\"apHandleCurrentChange\" :current-page=\"apCurrentPage\" :page-sizes=\"apPageSizes\" :page-size=\"apPageSize\" :total=\"apTotal\" layout=\"total, sizes, prev, pager, next\">\n            </el-pagination>\n          </div>\n        </div>\n      </el-tab-pane>\n    </el-tabs>\n    <!-- 添加、编辑进口许可证 -->\n    <el-dialog :title=\"editMode===1?'添加进口许可证':'编辑进口许可证'\" :visible.sync=\"inLicenseShow\" @open=\"beforeDialogOpen\">\n      <el-form :model=\"inLicenseModel\" :rules=\"inLicenseRules\" inline ref=\"inLicenseRef\" label-width=\"140px\" style=\"overflow-y:hidden;overflow-x:hidden;\">\n\n        <el-form-item label=\"进口商\" v-if=\"editMode===1\">\n          <el-select v-model=\"inLicenseModel.companyName\" clearable placeholder=\"请选择\" @change=\"onCompanyChange\" :disabled=\"editMode===1?ture:false\" style=\"width:320px\">\n            <el-option v-for=\"item in companys\" :key=\"item.id\" :label=\"item.name\" :value=\"item.name\">\n            </el-option>\n          </el-select>\n        </el-form-item>\n        <el-form-item label=\"进口商\" v-if=\"editMode===2\">\n          <el-input type=\"text\" v-model=\"inLicenseModel.companyName\" auto-complete=\"off\" class=\"input-320\" disabled></el-input>\n          </el-select>\n        </el-form-item>\n        <el-form-item label=\"许可证号\" prop=\"licenseKey\">\n          <el-input type=\"text\" v-model=\"inLicenseModel.licenseKey\" auto-complete=\"off\" class=\"input-320\"></el-input>\n        </el-form-item>\n        <el-form-item label=\"许可数量\" prop=\"count\">\n          <el-input-number v-model=\"inLicenseModel.count\" class=\"input-320\"></el-input-number>\n        </el-form-item>\n        <el-form-item label=\"商品编号\" prop=\"sku\">\n          <el-select class=\"e-input\" filterable v-model=\"inLicenseModel.sku\" placeholder=\"请选择\" style=\"width:320px\" @change=\"onInSkuChange\">\n            <el-option v-for=\"item in SKUData\" :key=\"item.sn\" :label=\"item.sn\" :value=\"item.sn\">\n              <span style=\"float: left\">{{ item.sn }}</span>\n              <span style=\"float: right; color: #8492a6; font-size: 13px\">{{ item.name }}</span>\n            </el-option>\n          </el-select>\n        </el-form-item>\n        <el-form-item label=\"许可证截止日期\" prop=\"expirationDateOfLicense\">\n          <el-date-picker v-model=\"inLicenseModel.expirationDateOfLicense\" type=\"date\" @change=\"inDateChangeClick\" placeholder=\"选择日期\" class=\"input-320\"></el-date-picker>\n        </el-form-item>\n        <el-form-item label=\"备注\" prop=\"memo\">\n          <el-input type=\"text\" v-model=\"inLicenseModel.memo\" auto-complete=\"off\" style=\"width:320px\"></el-input>\n        </el-form-item>\n\n      </el-form>\n      <div slot=\"footer\">\n        <el-button @click=\"inLicenseShow=false\">取消</el-button>\n        <el-button type=\"primary\" @click=\"inOkHandler\" :loading=\"confirmLoading\">确 定</el-button>\n      </div>\n    </el-dialog>\n\n    <!-- 添加、编辑出口许可证 -->\n    <el-dialog :title=\"outeditMode===1?'添加出口许可证':'编辑出口许可证'\" :visible.sync=\"outLicenseShow\" @open=\"beforeDialogOpen\">\n      <el-form :model=\"outLicenseModel\" :rules=\"outLicenseRules\" inline ref=\"outLicenseRef\" label-width=\"140px\" style=\"overflow-y:hidden;overflow-x:hidden;\">\n        <el-form-item label=\"出口商\" v-if=\"outeditMode===1\">\n          <el-select v-model=\"outLicenseModel.companyName\" clearable placeholder=\"请选择\" @change=\"onCompanyChange\" style=\"width:320px\">\n            <el-option v-for=\"item in companys\" :key=\"item.id\" :label=\"item.name\" :value=\"item.name\">\n            </el-option>\n          </el-select>\n        </el-form-item>\n        <el-form-item label=\"进口商\" v-if=\"outeditMode===2\">\n          <el-input type=\"text\" v-model=\"outLicenseModel.companyName\" auto-complete=\"off\" class=\"input-320\" disabled></el-input>\n          </el-select>\n        </el-form-item>\n        <el-form-item label=\"许可证号\" prop=\"licenseKey\">\n          <el-input type=\"text\" v-model=\"outLicenseModel.licenseKey\" auto-complete=\"off\" class=\"input-320\"></el-input>\n        </el-form-item>\n        <el-form-item label=\"许可数量\" prop=\"count\">\n          <el-input-number v-model=\"outLicenseModel.count\" class=\"input-320\"></el-input-number>\n        </el-form-item>\n        <el-form-item label=\"商品编号\" prop=\"sku\">\n          <el-select class=\"e-input\" filterable v-model=\"outLicenseModel.sku\" placeholder=\"请选择\" style=\"width:320px\" @change=\"onOutSkuChange\">\n            <el-option v-for=\"item in SKUData\" :key=\"item.sn\" :label=\"item.sn\" :value=\"item.sn\">\n              <span style=\"float: left\">{{ item.sn }}</span>\n              <span style=\"float: right; color: #8492a6; font-size: 13px\">{{ item.name }}</span>\n            </el-option>\n          </el-select>\n        </el-form-item>\n        <el-form-item label=\"许可证截止日期\" prop=\"expirationDateOfLicense\">\n          <el-date-picker v-model=\"outLicenseModel.expirationDateOfLicense\" @change=\"outDateChangeClick\" type=\"date\" placeholder=\"选择日期\" class=\"input-320\"></el-date-picker>\n        </el-form-item>\n        <el-form-item label=\"备注\" prop=\"memo\">\n          <el-input type=\"text\" v-model=\"outLicenseModel.memo\" auto-complete=\"off\" style=\"width:320px\"></el-input>\n        </el-form-item>\n      </el-form>\n      <div slot=\"footer\">\n        <el-button @click=\"outLicenseShow=false\">取消</el-button>\n        <el-button type=\"primary\" @click=\"outOkHandler\" :loading=\"confirmLoading\">确 定</el-button>\n      </div>\n    </el-dialog>\n\n  </div>\n</template>\n\n<script>\nimport './mock/license.js';\nimport licenseAPI from './api/licenseAPI.js';\nimport licensegoodsTable from './components/licensegoodsTable.vue';\nimport skuAPI from './api/skuAPI.js';\nimport companyAPI from './api/companyAPI.js';\nexport default {\n  data() {\n    return {\n      clientHeight: 0,\n      clientWidth: 0,\n      /*我的申请表格*/\n      inLicenseData: [],\n      /*待我审批表格*/\n      outLicenseData: [],\n      /*我的申请数据模型*/\n      inLicenseModel: {\n        name: '',\n        manager: '',\n        description: '',\n        starttime: '',\n        endtime: '',\n      },\n      outLicenseModel: {\n        name: '',\n        manager: '',\n        description: '',\n        starttime: '',\n        endtime: '',\n      },\n      /*进口搜索*/\n      insearch: {\n        licenseKey: '',\n        importAndExportcode: '',\n        companyName: '',\n        type: 'in',\n      },\n      /*出口搜索*/\n      outsearch: {\n        licenseKey: '',\n        importAndExportcode: '',\n        companyName: '',\n        type: 'out',\n      },\n      /*激活的tab页 */\n      activeName: 'inlicense',\n      /*我的申请表单校验规则 */\n      inLicenseRules: {\n        name: [{ required: true, message: '请输入项目名称', trigger: 'blur' }],\n        manager: [{ required: true, message: '请输入项目负责人', trigger: 'blur' }],\n      },\n      /*编辑添加框是否可见 */\n      inLicenseShow: false,\n      /*编辑添加框是否可见 */\n      outLicenseShow: false,\n      /*文件列表查看、编辑框是否可见 */\n      myFileDialogIsShow: false,\n      /*文件上传框是否可见 */\n      fileUploadDialogIsShow: false,\n      /*文件预览框是否显示 */\n      fileViewDialogIsShow: false,\n      /*审批进度 */\n      failDialogIsShow: false,\n      passDialogIsShow: false,\n      loadDialogIsShow: false,\n      /*进口许可证编辑模式：1添加，2编辑*/\n      editMode: 1,\n      /*出口许可证编辑模式：1添加，2编辑*/\n      outeditMode: 1,\n      /*文件类型：1申报文件，2审批文件 */\n      fileType: 1,\n      /*表单提交等待动画*/\n      confirmLoading: false,\n      /*我的申请表格选中行*/\n      inSelectedRows: [],\n      /*待我审核表格选中行*/\n      outSelectedRows: [],\n      /*文件表格选中行 */\n      fileSelectedRows: [],\n      /*开始日期初始化 */\n      startdatepicker: '',\n      /*结束日期初始化 */\n      enddatepicker: '',\n      /*我的申请当前页 */\n      myCurrentPage: 1,\n      /*我的申请表格可选页面大小*/\n      myPageSizes: [5, 10, 15, 20],\n      /*我的申请表格当前页面大小 */\n      myPageSize: 10,\n      /*我的申请表格数据总条数 */\n      myTotal: 16,\n      /*待我审核当前页 */\n      apCurrentPage: 1,\n      /*待我审核表格可选页面大小*/\n      apPageSizes: [5, 10, 15, 20],\n      /*待我审核表格当前页面大小 */\n      apPageSize: 10,\n      /*待我审核表格数据总条数 */\n      apTotal: 21,\n      /*文件列表数据 */\n      fileTableData: [],\n      /*文件上传初始化 */\n      fileList: [],\n      /*进度条百分比 */\n      percentage: 0,\n      // i: 0,\n      SKUData: [],\n      companys: {}\n\n    };\n  },\n  methods: {\n    onInSkuChange(val) {\n      let skuName = '';\n      this.SKUData.forEach(function(row) {\n        if (row.sn == val) {\n          skuName = row.name;\n          return;\n        }\n      });\n      this.inLicenseModel.skuName = skuName;\n    },\n    onOutSkuChange(val) {\n      let skuName = '';\n      this.SKUData.forEach(function(row) {\n        if (row.sn == val) {\n          skuName = row.name;\n          return;\n        }\n      });\n      this.outLicenseModel.skuName = skuName;\n    },\n    //单击一行选中当前行、单击多选框增加选中当前行\n    onInRowClick(row, event, column) {\n      if (column.type != \"selection\") {\n        this.$refs.myTabelRef.clearSelection();\n      }\n      this.$refs.myTabelRef.toggleRowSelection(row);\n    },\n    onOutRowClick(row, event, column) {\n      if (column.type != \"selection\") {\n        this.$refs.apTabelRef.clearSelection();\n      }\n      this.$refs.apTabelRef.toggleRowSelection(row);\n    },\n    //双击\n    dbInEditClick(row) {\n      this.editMode = 2;\n      this.inLicenseShow = true;\n      this.inLicenseModel = Object.assign({}, row);\n    },\n    dbOutEditClick(row) {\n      this.outeditMode = 2;\n      this.outLicenseShow = true;\n      this.outLicenseModel = Object.assign({}, row);\n    },\n    //格式化时间 格式化时间为yyyy-MM-dd hh:mm:ss\n    formatDate(date, fmt) {\n      if (/(y+)/.test(fmt)) {\n        fmt = fmt.replace(\n          RegExp.$1,\n          (date.getFullYear() + '').substr(4 - RegExp.$1.length)\n        )\n      }\n      let o = {\n        'M+': date.getMonth() + 1,\n        'd+': date.getDate(),\n        'h+': date.getHours(),\n        'm+': date.getMinutes(),\n        's+': date.getSeconds()\n      }\n      for (let k in o) {\n        if (new RegExp(`(${k})`).test(fmt)) {\n          let str = o[k] + ''\n          fmt = fmt.replace(\n            RegExp.$1,\n            RegExp.$1.length === 1 ? str : ('00' + str).substr(str.length)\n          )\n        }\n      }\n      return fmt\n    },\n    outDateChangeClick() {\n      if (this.outLicenseModel.expirationDateOfLicense) {\n        this.outLicenseModel.expirationDateOfLicense = this.formatDate(new Date(this.outLicenseModel.expirationDateOfLicense), 'yyyy-MM-dd');\n      } else {\n        this.outLicenseModel.expirationDateOfLicense = '';\n      }\n    },\n    inDateChangeClick() {\n      if (this.inLicenseModel.expirationDateOfLicense) {\n        this.inLicenseModel.expirationDateOfLicense = this.formatDate(new Date(this.inLicenseModel.expirationDateOfLicense), 'yyyy-MM-dd');\n      } else {\n        this.inLicenseModel.expirationDateOfLicense = '';\n    